.app__projects {
  flex: 1;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  background-color: var(--white-color);
  transition: all .2s ease-in-out;

  h2 {
    padding-top: 6rem;
  }
}

.app__projects-filter {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 4rem 0 3rem;

  .app__projects-filter-item {
    cursor: pointer;
    color: var(--black-color);
    background-color: var(--white-color);
    padding: 0.5rem 1rem;
    margin: 0.5rem;
    font-weight: 800;
    text-transform: capitalize;
    border-radius: 0.5rem;
    box-shadow: var(--light-shadow);
    transition: all 0.5s ease;

    &:hover {
      background-color: var(--secondary-color);
      color: #fff;
    }

    @media screen and (min-width: 2000px) {
      padding: 1rem 2rem;
      border-radius: 0.8rem;
    }
  }

  .item-active {
    background-color: var(--secondary-color);
    color: #fff;
  }
}

.app__projects-items {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: flex-end;

  .app__projects-item {
    width: 350px;
    margin: 2rem;
    padding: 1.5rem;
    flex-direction: column;
    border-radius: 0.5rem;
    color: var(--black-color);
    background-color: var(--white-color);
    transition: all 0.4s ease;
    box-shadow: var(--medium-shadow);

    @media screen and (min-width: 2000px) {
      width: 470px;
      padding: 1.25rem;
      border-radius: 0.75rem;
    }

    @media screen and (max-width: 300px) {
      width: 100%;
      margin: 1rem;
    }
  }
}

.app__projects-img {
  width: 100%;
  height: 250px;
  position: relative;

  img {
    height: 100%;
    width: 100%;
    border-radius: 0.5rem;
    object-fit: cover;
  }

  .app__projects-hover {
    opacity: 0;
    border-radius: 0.5rem;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    transition: all 0.4s ease;

    div {
      width: 50px;
      height: 50px;
      color: var(--white-color);
      background-color: rgba(0, 0, 0, 0.4);
      border-radius: 50%;
      margin: 1rem;
      font-family: (--font-base);
      cursor: pointer;
      transition: all 0.3s ease;

      svg {
        width: 50%;
        height: 50%;
        color: var(--white-color);
      }
    }

    &:hover {
      opacity: 1;
    }
  }

  @media screen and (min-width: 2000px) {
    height: 400px;
  }
}

.app__projects-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;

  h4 {
    margin-top: 1rem;
    line-height: 1.5;

    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }

  p {
    text-align: center;
    font-size: 1.1rem;
  }
}


.dark-mode-1 {
  background-color: var(--darkbg-color-1);

  h2 {
    color: var(--lightGray-color);  
  }

  .app__projects-filter-item {
    background-color: var(--darkbg-color-2);
    border: solid 1px var(--light-border-color);
    color: var(--lightGray-color);
    box-shadow: 0 0 0;        
  }

  .app__projects-filter-item:hover, .item-active {
    background-color: var(--lightGray-color);
    color: var(--black-color);
  }

  .app__projects-item {
    background-color: var(--darkbg-color-2);
    box-shadow: 0 0 0;
    border: solid 1px var(--light-border-color);

    .app__projects-content {
      h4, p {
        color:var(--lightGray-color)
      }
    }

    .app__projects-hover:hover {
      background-color: rgba(186, 181, 181, 0.2);
    }
  }

}